"use client";
import { useState } from "react";
import { BigNumber, ethers } from "ethers";
import {
  ChakraBaseProvider,
  Box,
  Text,
  Input,
  Button,
  Flex,
  Spacer,
} from "../setting/component";
import kokoToken from "../artifacts/contracts/kokoDao/kokoToken.sol/kokoToken.json";

// 
var tokenContract;

const MintCoin = () => {
  //  React 
  const [mintAmount, setMintAmount] = useState(50); // 
  const [name, setName] = useState("name"); //  Token 
  const [symbol, setSymbol] = useState("symbol"); //  Token 

  //  Token 
  async function deployToken() {
    if (window.ethereum) {
      //  MetaMask ethers  provider
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const signerAddress = await signer.getAddress();
      console.log("mintCoin-initMint()-start");

      try {
        // 
        const tokenFactory = new ethers.ContractFactory(
          kokoToken.abi,
          kokoToken.bytecode,
          signer
        );
        tokenContract = await tokenFactory.deploy(name, symbol);
        await tokenContract.deployed();
        console.log("tokenContract address:", tokenContract.address);
      } catch (err) {
        console.log("initMint error", err); // 
      }
    } else {
      alert("MetaMask  !!!!"); //  MetaMask
    }
  }

  // 
  async function handleMint() {
    if (window.ethereum) {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      const signerAddress = await signer.getAddress();
      //  
      const mintResponce = await tokenContract
        .connect(signer)
        .mint(BigNumber.from(mintAmount));
      const receipt = await mintResponce.wait();
      console.log("handleMint()-receipt", receipt);
      // 
      const balance = await tokenContract.balanceOf(signerAddress);
      console.log("user balance change to: ", balance.toString());
    } else {
      alert("MetaMask  !!!!");
    }
  }

  // 
  async function verifyContract() {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const signerAddress = await signer.getAddress();
    const balance = await tokenContract.balanceOf(
      signerAddress // 
    );
    const name = await tokenContract.name(); //  Token 
    console.log(`token name:${name} ,user balance : ${balance}`); // 
  }

  //  
  const decreaseOnclick = () => {
    if (mintAmount <= 1) return; //  1
    setMintAmount(mintAmount - 1); // 
  };

  //  
  const increaseOnclick = () => {
    if (mintAmount >= 200) return; //  200
    setMintAmount(mintAmount + 1); // 
  };

  return (
    <div>
      <ChakraBaseProvider>
        <Flex
          justify="center" // 
          align="center" // 
          height="100vh" // 
          paddingBottom="180px"
        >
          <Box width="520px">
            {/*  */}
            <div id="init-and-mint" className="mintpage">
              <div id="init">
                <Text fontSize="40px" padding="50px">
                  Mint Coin
                </Text>
                <Flex
                  id="init-input"
                  align="center"
                  justify="center"
                  padding="15px"
                  textColor="black"
                >
                  {/*  Token  */}
                  <Input
                    type="text"
                    defaultValue={name}
                    onChange={(e) => setName(e.target.value)}
                    placeholder="Token Name"
                  />

                  {/*  Token  */}
                  <Input
                    type="text"
                    defaultValue={symbol}
                    onChange={(e) => setSymbol(e.target.value)}
                    placeholder="Token Symbol"
                  />
                </Flex>
                {/*  */}
                <Button id="init-deploy-button" onClick={deployToken}>
                  Init deploy token Contract
                </Button>
              </div>
              {/*  */}
              <div id="mint">
                <Flex
                  id="mint-setAmount"
                  align="center"
                  justify="center"
                  padding="30px"
                  textColor="black"
                >
                  {/*  */}
                  <Button onClick={decreaseOnclick}>-</Button>
                  {/*  */}
                  <Input
                    type="number"
                    value={mintAmount} // 
                    onChange={(e) => setMintAmount(e.target.value)}
                  />
                  {/*  */}
                  <Button onClick={increaseOnclick}>+</Button>
                </Flex>
                {/*  */}
                <Button id="mint-button" onClick={handleMint}>
                  Mint
                </Button>
                {/*  */}
                <Button onClick={verifyContract}>verify!</Button>
              </div>
            </div>
          </Box>
        </Flex>
      </ChakraBaseProvider>
    </div>
  );
};

export default MintCoin;
