import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  ChakraBaseProvider,
  Box,
  Text,
  Input,
  Button,
  Flex,
  Spacer,
  Progress,
} from "../setting/component";
import { proposeLog } from "../scripts/kokoDao/02-propose"; // 
import { voteLog } from "../scripts/kokoDao/03-vote"; // 
import { moveBlocks } from "../scripts/kokoDao/01-deploy"; //  moveBlocks 

const ProposalData = () => {
  const navigate = useNavigate();

  // 
  const proposalDetailClick = () => {
    navigate("/ProposalDetail");
  };

  //  React 
  const [proposalDataList, setProposalDataList] = useState([]); // 
  const [refreshFlag, setRefreshFlag] = useState(false); // 

  // 
  const getSupportStatistics = (proposalId) => {
    // 
    const proposalVotes = voteLog.filter(
      (vote) => vote.proposalId === proposalId
    );
    const totalVotes = proposalVotes.length; // 
    const supportStats = { oppose: 0, support: 0, abstain: 0 }; // 

    // 
    proposalVotes.forEach((vote) => {
      if (vote.support === "0") supportStats.oppose += 1;
      if (vote.support === "1") supportStats.support += 1;
      if (vote.support === "2") supportStats.abstain += 1;
    });

    // 
    return {
      support: ((supportStats.support / totalVotes) * 100).toFixed(1) || 0,
      oppose: ((supportStats.oppose / totalVotes) * 100).toFixed(1) || 0,
      abstain: ((supportStats.abstain / totalVotes) * 100).toFixed(1) || 0,
    };
  };

  // 
  useEffect(() => {
    setProposalDataList(proposeLog); //  proposeLog 
  }, [refreshFlag]); // 

  //  5 
  useEffect(() => {
    const interval = setInterval(() => {
      setRefreshFlag((prevFlag) => !prevFlag); // 
    }, 5000);

    return () => clearInterval(interval); // 
  }, []);

  // moveBlocks 
  async function moveBlocksOnclick() {
    await moveBlocks(1);
  }

  return (
    <div>
      <ChakraBaseProvider>
        <Box margin="10px">
          {proposalDataList.map((proposalData) => {
            // 
            const { support, oppose, abstain } = getSupportStatistics(
              proposalData.proposalId
            );

            return (
              <Box
                key={proposalData.proposalId} //  ID 
                border="1px solid #E2E8F0"
                borderRadius="md"
                padding="8px"
                margin="30px 20px"
                maxW="380px"
                // maxH="1000px"
                boxShadow="md"
              >
                {/*  */}
                <Text fontSize="12px" color="gray.500">
                  {proposalData.timestamp}
                </Text>

                {/*  */}
                <Button
                  size="md"
                  variant="outline"
                  marginTop="3"
                  whiteSpace="normal"
                  wordBreak="break-all"
                  onClick={proposalDetailClick}
                >
                  Proposal Description: {proposalData.description}
                </Button>

                {/*  ID */}
                <Text fontSize="13px" color="gray.500" marginTop="2">
                  Proposal ID: {proposalData.proposalId}
                </Text>

                {/*  */}
                <Text fontSize="sm" color="gray.500" marginTop="2">
                   POLL ENDED
                </Text>

                {/*  */}
                <Flex direction="column" marginTop="1">
                  <Box
                    position="relative"
                    width="100%"
                    height="10px"
                    borderRadius="md"
                    backgroundColor="gray.200"
                    overflow="hidden"
                  >
                    {/*  */}
                    <Box
                      position="absolute"
                      top="0"
                      left="0"
                      height="100%"
                      width={`${oppose}%`}
                      backgroundColor="red.600"
                    ></Box>
                    {/*  */}
                    <Box
                      position="absolute"
                      top="0"
                      left={`${oppose}%`}
                      height="100%"
                      width={`${support}%`}
                      backgroundColor="blue.600"
                    ></Box>
                    {/*  */}
                    <Box
                      position="absolute"
                      top="0"
                      right="0"
                      height="100%"
                      width={`${abstain}%`}
                      backgroundColor="yellow.400"
                    ></Box>
                  </Box>

                  {/*  % */}
                  <Flex justifyContent="space-between" marginTop="2">
                    <Text fontSize="sm" color="gray.300">
                      Support {support}%
                    </Text>
                    <Text fontSize="sm" color="gray.300">
                      Oppose {oppose}%
                    </Text>
                    <Text fontSize="sm" color="gray.300">
                      Abstain {abstain}%
                    </Text>
                  </Flex>
                </Flex>

                {/*  */}
                <Button
                  size="md"
                  variant="outline"
                  marginTop="3"
                  whiteSpace="normal"
                  wordBreak="break-all"
                >
                  Transaction Hash:{" "}
                  {proposalData.transactionHash
                    ? proposalData.transactionHash.slice(0, 5) + "..."
                    : ""}
                </Button>
              </Box>
            );
          })}

          {/*  */}
          <Button
            fontSize="18px"
            margin="10px"
            onClick={() => setRefreshFlag((prevFlag) => !prevFlag)}
          >
            Refresh
          </Button>

          {/*  moveBlocks */}
          <Button fontSize="18px" margin="10px" onClick={moveBlocksOnclick}>
            moveBlocks
          </Button>
        </Box>
      </ChakraBaseProvider>
    </div>
  );
};

export default ProposalData; //  ProposalData 
