// Koko.js
import React, { useState } from "react";
import {
  ChakraBaseProvider,
  Box,
  Text,
  Input,
  Button,
  Flex,
  Spacer,
} from "../setting/component"; //  Chakra UI 
import NavBar from "./00-NavBar";
import deploy from "../scripts/kokoDao/01-deploy";
import propose from "../scripts/kokoDao/02-propose";
import vote from "../scripts/kokoDao/03-vote";
import execute from "../scripts/kokoDao/04-execute";
import ProposalData from "./proposalData";

/**
 * kokoWorld DAO 
 */
function Kokoworld({ accounts, setAccounts }) {
  // 
  const [inputValue1, setInputValue1] = useState(""); // 
  const [inputValue2, setInputValue2] = useState("0"); // 
  const [inputValue3, setInputValue3] = useState("set proposal description"); // 

  const [proposalidInput, setproposalidInput] = useState(""); //  ID 
  const [supportInput, setsupportInput] = useState("1"); // 
  const [accountNumberInput, setaccountNumberInput] = useState("0"); // 

  const [executeProposalIdInput, setexecuteProposalIdInput] = useState(""); //  ID 

  //  
  async function deployOnclick() {
    if (typeof window.ethereum !== "undefined") {
      try {
        await deploy(); // 
        alert(""); // 
      } catch (error) {
        console.error("", error);
        alert("");
      }
    } else {
      alert("MetaMask  MetaMask");
    }
  }

  async function proposeOnclick() {
    if (!inputValue1 || !inputValue3) {
      alert(""); // 
      return;
    }
    if (typeof window.ethereum !== "undefined") {
      try {
        await propose(inputValue1, inputValue3); // 
        alert(""); // 
      } catch (error) {
        console.error("", error);
        alert("");
      }
    } else {
      alert("MetaMask  MetaMask");
    }
  }
  //  
  async function voteOnclick() {
    if (!proposalidInput || !supportInput || !accountNumberInput) {
      alert(" ID"); // 
      return;
    }
    if (typeof window.ethereum !== "undefined") {
      try {
        await vote(proposalidInput, supportInput, accountNumberInput); // 
        alert(""); // 
      } catch (error) {
        console.error("", error);
        alert("");
      }
    } else {
      alert("MetaMask  MetaMask");
    }
  }

  //  
  async function executeOnclick() {
    if (!executeProposalIdInput) {
      alert(" ID "); // 
      return;
    }
    if (typeof window.ethereum !== "undefined") {
      try {
        await execute(executeProposalIdInput); // 
        alert(""); // 
      } catch (error) {
        console.error("", error);
        alert("");
      }
    } else {
      alert("MetaMask  MetaMask");
    }
  }

  return (
    <div style={{ height: "100vh", overflowY: "auto" }}>
      {/*  */}
      <NavBar accounts={accounts} setAccounts={setAccounts} />
      <ChakraBaseProvider>
        {/*  */}
        <Flex
          justify="center" // 
        >
          {/*  */}
          <ProposalData />
          <div className="run-Dao">
            <Box width="900px" margin="50px 0px">
              <Box id="DAO_title" margin="auto">
                <Text fontSize="40px">kokoWorld DAO!!</Text>
                <Text
                  fontSize="33px"
                  letterSpacing="-5.5%" // 
                  fontFamily="VT323" // 
                  textShadow="0 2px 2px #000000"
                >
                  It's 2077, bala bala ...
                </Text>
              </Box>
              {/* deploy  */}
              <button onClick={deployOnclick}>deploy</button>
              {/*  */}
              <div id="propose">
                <Flex
                  id="propose-input"
                  align="center"
                  justify="center"
                  padding="20px 30px"
                  textColor="black"
                >
                  {/*  */}
                  <Input
                    type="text"
                    value={inputValue1}
                    onChange={(e) => setInputValue1(e.target.value)}
                    placeholder={"proposal exe"}
                  ></Input>
                  {/*  */}
                  <Input
                    type="text"
                    value={inputValue2}
                    onChange={(e) => setInputValue2(e.target.value)}
                    placeholder={"proposal value"}
                  ></Input>
                  {/*  */}
                  <Input
                    type="text"
                    defaultValue={inputValue3}
                    onChange={(e) => setInputValue3(e.target.value)}
                  ></Input>
                </Flex>
                {/*  */}
                <button id="propose-button" onClick={proposeOnclick}>
                  propose
                </button>
              </div>
              {/*  */}
              <div id="vote">
                <Flex
                  id="propose-input"
                  align="center"
                  justify="center"
                  padding="20px 30px"
                  textColor="black"
                >
                  {/*  ID  */}
                  <Input
                    type="text"
                    defaultValue={proposalidInput}
                    onChange={(e) => setproposalidInput(e.target.value)}
                    placeholder={"proposal Id"}
                  ></Input>
                  {/*  */}
                  <Input
                    type="number"
                    defaultValue={supportInput}
                    onChange={(e) => setsupportInput(e.target.value)}
                    placeholder={"support"}
                  ></Input>
                  {/*  */}
                  <Input
                    type="number"
                    defaultValue={accountNumberInput}
                    onChange={(e) => setaccountNumberInput(e.target.value)}
                    placeholder={"0"}
                  ></Input>
                </Flex>
                {/*  */}
                <button id="vote-button" onClick={voteOnclick}>
                  vote
                </button>
              </div>
              {/*  */}
              <div id="execute">
                <Flex
                  id="propose-input"
                  align="center"
                  justify="center"
                  padding="20px 30px"
                  textColor="black"
                >
                  {/*  ID  */}
                  <Input
                    type="text"
                    defaultValue={executeProposalIdInput}
                    onChange={(e) => setexecuteProposalIdInput(e.target.value)}
                    placeholder={"execute ProposalId"}
                  ></Input>
                </Flex>
                {/*  */}
                <button id="execute-button" onClick={executeOnclick}>
                  execute
                </button>
              </div>
            </Box>
          </div>
        </Flex>
      </ChakraBaseProvider>
    </div>
  );
}

export default Kokoworld;
